@import '_styles/variables';
@import '_styles/mixins';

.HomeRoute {

  &__banner {
    position: relative;
    padding: calc(12px + 2vw) 0;
    background: transparent url('../_images/misc/bannerPattern.png') repeat;
  }

  &__bannerGrid {
    grid-auto-flow: row;

    @include respondAbove(s) {
      grid-auto-flow: column;
    }
  }

  &__bannerText {
    align-self: center;

    h1 {
      margin: 0 0 20px;
    }

    p {
      margin-bottom: 0;

      @include respondAbove(s) {
        margin-bottom: 5vw;
      }
    }
  }

  &__skills {
    background-color: rgba(#222426, 0.08);
    padding: calc(12px + 2vw) 0;
  }

  &__skillsGrid {
    display: grid;
    gap: calc(12px + 2vw);

    @include respondAbove(m) {
      grid-template-columns: 1fr 1fr;
    }

    h2 {
      margin-top: 0.5em;
    }

    p {

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__skillsItem {

    &--divider {
      display: none;

      hr {
        border: 0;
        background-color: rgba(#222426, 0.1);
        margin: 0;
        height: 1px;
      }
    }

    @include respondAbove(m) {
      padding-right: 4vw;

      &--divider {
        display: block;
        grid-column: span 2;
      }
    }
  }
}
